.Headd {
    opacity: 0;
    pointer-events: none;
  }

  .load-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    z-index: 10;
    pointer-events: none;
  }
  
  .load-screen {
    position: relative;
    padding-top: 0px;
    padding-left: 0px;
    padding-right: 0px;
    background-color: #fff;
    width: 100%;
    height: 100%;
    opacity: 1;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .load-screen video {
    width: 100px;
}
.animate_logo_dv {
  width: 100px;
  height: 100px;
  position: relative;
  overflow: hidden;
}
.animate_logo_dv img {
  width: 97%;
}
.animate_logo_sld {
  position: absolute;
  bottom: 0;
  left: 0%;
  width: 100%;
  height: 100%;
  background: #fff;
  transition: all 0.2s;
}

p#thnk_box1 {
  text-align: left;
}


/*---------------------------------------------------------------- Mobile CSS */
@media only screen and (min-device-width: 320px) and (max-device-width: 600px) {

  .animate_logo_dv {
    width: 70px;
    height: 70px;
  }
  
}




